/* roboto-regular */

body {
    margin: 0px;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #f6f6f6;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-weight: normal;
    color: #6b6b6b;
    -webkit-font-smoothing: antialiased;
    font-size: 16px;
}

#layout-topbar {
    background-color: #20272a;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: block;
    padding: 0;
    height: 70px;
    box-sizing: border-box;
    /* position: fixed; */
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
}

#layout-topbar .menu-button {
    display: none;
    color: #ffffff;
    font-size: 24px;
    position: absolute;
    left: 0;
    top: 0;
    width: 60px;
    height: 60px;
    line-height: 58px;
    text-align: center;
    -webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
}

#layout-topbar .menu-button:hover {
    background-color: #4a4f52;
}

#layout-topbar .menu-button i {
    line-height: inherit;
}

#layout-topbar .logo {
    margin-left: 45px;
    margin-top: 12px;
    display: inline-block;
}

#layout-topbar .logo img {
    width: 210px;
}

#layout-topbar .topbar-menu {
    display: inline-block;
    list-style-type: none;
    float: right;
    margin: 0 60px 0 0;
    padding: 0;
    height: 100%;
}

#layout-topbar .topbar-menu>li {
    display: inline-block;
    height: 100%;
}

#layout-topbar .topbar-menu>li>a {
    text-decoration: none;
    color: #ffffff;
    -webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
    min-width: 120px;
    display: inline-block;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    height: 100%;
    line-height: 70px;
}

#layout-topbar .topbar-menu>li>a:hover {
    background-color: #4a4f52;
}

#layout-topbar .topbar-menu>li>ul {
    display: none;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes {
    position: relative;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul {
    position: absolute;
    top: 65px;
    left: -75px;
    width: 250px;
    max-height: 300px;
    background-color: #ffffff;
    -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    overflow: auto;
    list-style-type: none;
    padding: 15px 0;
    margin: 0;
    border-radius: 3px;
    -webkit-animation-name: fadeInDown;
    animation-name: fadeInDown;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul>li.topbar-submenu-header {
    padding: 6px 12px;
    font-weight: bold;
    text-align: left;
    background-color: #008fba;
    background-image: -webkit-linear-gradient(left, #008fba, #03c4e8);
    background-image: -o-linear-gradient(left, #008fba, #03c4e8);
    background-image: -webkit-gradient(linear, left top, right top, from(#008fba), to(#03c4e8));
    background-image: linear-gradient(to right, #008fba, #03c4e8);
    filter: progid: DXImageTransform.Microsoft.gradient(startColorStr='#008fba', endColorStr='#03c4e8', gradientType='1');
    color: #ffffff;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul a {
    text-decoration: none;
    color: #404C51;
    padding: 6px 12px;
    display: block;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul a:hover {
    background-color: #eeeeee;
    cursor: pointer;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul a img {
    width: 32px;
    vertical-align: middle;
    margin-right: 4px;
}

#layout-topbar .topbar-menu>li.topbar-menu-themes>ul a .ui-text {
    vertical-align: middle;
    margin-left: 8px;
}

#layout-topbar .topbar-menu>li:hover>ul {
    display: block;
}

#layout-sidebar {
    position: fixed;
    left: 0;
    top: 70px;
    height: 100%;
    background-color: #ffffff;
    overflow: hidden;
    width: 300px;
    -webkit-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

#layout-sidebar .layout-menu>a {
    width: 100%;
    height: 50px;
    display: block;
    padding: 15px 0px 0px 25px;
    border-top: solid 1px #e3e9ea;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #484848;
    cursor: pointer;
}

#layout-sidebar .layout-menu>a:hover {
    background-color: #eeeeee;
}

#layout-sidebar .layout-menu>a span {
    font-size: 16px;
    margin: 0px 0px 0px 30px;
    display: block;
}

#layout-sidebar .layout-menu>a img {
    width: 20px;
    height: auto;
    float: left;
}

#layout-sidebar .layout-menu>a img.layout-menu-icon-active {
    display: none;
}

#layout-sidebar .layout-menu>a img.layout-menu-icon-inactive {
    display: inline;
}

#layout-sidebar .layout-menu>a.active-menuitem {
    background-color: #ffffff;
    color: #03c4e8;
}

#layout-sidebar .layout-menu>a.active-menuitem img.layout-menu-icon-active {
    display: inline;
}

#layout-sidebar .layout-menu>a.active-menuitem img.layout-menu-icon-inactive {
    display: none;
}

#layout-sidebar .layout-menu>div {
    width: auto;
    overflow: hidden;
    background-color: #ffffff;
}

#layout-sidebar .layout-menu>div>div {
    overflow: hidden;
    padding: 20px 15px;
}

#layout-sidebar .layout-menu>div.submenuhide {
    overflow: hidden;
    max-height: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 0;
    margin-bottom: 0;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    -o-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
}

#layout-sidebar .layout-menu>div.submenushow {
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    -o-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
    max-height: 500px;
}

#layout-sidebar .layout-menu>div a {
    width: 50%;
    float: left;
    padding: 6px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: pointer;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    font-size: 14px;
    color: #7a8687;
}

#layout-sidebar .layout-menu>div a:hover {
    background-color: #eeeeee;
}

#layout-sidebar .nano>.nano-content .layout-menu {
    padding-bottom: 120px;
}

