@media screen and (max-width: 64em) {
    #layout-topbar {
        text-align: center;
    }
    #layout-topbar .menu-button {
        display: inline-block;
    }
    #layout-topbar .logo {
        margin: 7px 0 7px 0;
    }
    #layout-topbar .logo img {
        width: 180px;
    }
    #layout-topbar .topbar-menu {
        background-color: #363c3f;
        float: none;
        width: 100%;
        height: 40px;
        margin: 0;
        text-align: center;
    }
    #layout-topbar .topbar-menu>li>a {
        padding-bottom: 0;
        line-height: 40px;
        min-width: 100px;
    }
    #layout-topbar .topbar-menu>li.topbar-menu-themes>ul {
        top: 40px;
        text-align: left;
    }
    #layout-sidebar {
        top: 98px;
        left: -300px;
        -webkit-transition: left .3s;
        -o-transition: left .3s;
        transition: left .3s;
        z-index: 999;
    }
    #layout-sidebar.active {
        left: 0;
    }
    #layout-content {
        margin-left: 0;
        padding-top: 96px;
    }
    #layout-content .content-section.submenu ul li {
        width: 50%;
    }
    #layout-content .content-section.source .doc-table thead th,
    #layout-content .content-section.source .doc-table tbody td {
        word-break: break-all;
    }
    .home .introduction>div {
        width: 100%;
    }
    .home .features>div {
        width: 100%;
    }
    .home .whouses>div {
        width: 100%;
    }
    .home .prosupport>div {
        width: 100%;
    }
}

