    border-radius: 0;
    opacity: 1;
    -webkit-transition: background-color .3s;
    -o-transition: background-color .3s;
    transition: background-color .3s;
}

.scrollpanel-demo .custombar2:hover .ui-scrollpanel-bar {
    background-color: #46A55A;
}


/* Animation */

@-webkit-keyframes fadeInDown {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, -20px, 0);
        transform: translate3d(0, -20px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        -webkit-transform: translate3d(0, -20px, 0);
        transform: translate3d(0, -20px, 0);
    }
    to {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}


/* Demos */

.datalist-demo .car-item {
    padding: 1.5em;
    padding: 20px;
    border-bottom: 1px solid #cccccc;
}

.datalist-demo .car-item i {
    cursor: pointer;
    float: left;
    margin-top: 40px;
    margin-right: 20px;
}

.datalist-demo li:last-child .car-item {
    border-bottom: 0 none;
}

.datalist-demo .ui-g>div {
    padding: .4em;
}

.datalist-demo .ui-g .ui-g-10,
.datalist-demo .ui-g .ui-g-8 {
    font-weight: bold;
}

.ui-growl {
    top: 100px !important;
}

pre code {
    border-radius: 4px;
    -webkit-border-radius: 4px;
    font-size: 14px;
    background-color: #20272a !important;
}

@media (max-width: 40em) {
    .datalist-demo .car-item {
        text-align: center;
    }
    .datalist-demo .car-item i {
        float: none;
        display: inline-block;
        vertical-align: top;
    }
}


